import { useEffect, useState, useCallback, useMemo, useRef } from "react";
import {
  ReactFlow,
  Background,
  useNodesState,
} from "@xyflow/react";
import "@xyflow/react/dist/style.css";
import { motion, AnimatePresence } from "framer-motion"; // Add AnimatePresence
import startSound from "./assets/Mario 1 - Mario Riff.mp3";
import shoot from "./assets/fireball.mp3";
import explosion from "./assets/Break_Brick.mp3";
import gameWin from "./assets/game-win.mp3";
import gameOver from "./assets/die.mp3";
import GameOverModal from "./components/leaderboard/GameOverModal";

const Button = ({ style = {}, children, ...rest }) => (
  <button
    {...rest}
    style={{ 
      padding: "8px 12px", 
      borderRadius: "4px", 
      backgroundColor: "#404040", 
      border: "none",
      color: "white",
      cursor: "pointer",
      fontSize: "14px",
      transition: "background-color 0.2s, transform 0.2s",
      ...style
    }}
    onMouseOver={(e) => { e.currentTarget.style.backgroundColor = "#525252"; }}
    onMouseOut={(e) => { e.currentTarget.style.backgroundColor = "#404040"; }}
    onMouseDown={(e) => { e.currentTarget.style.transform = "scale(0.95)"; }}
    onMouseUp={(e) => { e.currentTarget.style.transform = "scale(1)"; }}
    onTouchStart={(e) => { e.currentTarget.style.transform = "scale(0.95)"; }}
    onTouchEnd={(e) => { e.currentTarget.style.transform = "scale(1)"; }}
  >
    {children}
  </button>
);

/* ------------------------------ constants ------------------------------ */
const SOUNDS = {
  shoot: shoot,
  explosion: explosion,
  gameStart: startSound,
  gameOver: gameOver,
  gameWin: gameWin,
};

const loadSound = (url) => {
  const audio = new Audio(url);
  audio.load();
  return audio;
};

const getGameDimensions = () => {
  const isMobile = window.innerWidth < 768;
  const spacingX = isMobile ? 160 : 160;
  const cols = 10;
  const margin = 200; // same offset you use in initGame
  const invaderWidth = 40;
  const totalWidth = margin + cols * spacingX + invaderWidth;
  const zoom = isMobile ? 0.99 : 0.95;
  return {
    isMobile,
    width: isMobile ? (window.innerWidth + 500) : Math.max(window.innerWidth, totalWidth),
    height: isMobile ? 500 : 800,
    playerY: isMobile ? 1000 : 800,
    zoom,
    totalWidth,

    invaderSpacingX: spacingX,
    invaderSpacingY: isMobile ? 80 : 140,
  };
};

const dimensions = getGameDimensions();
const { width, height, playerY, zoom, totalWidth, invaderSpacingX, invaderSpacingY } = dimensions;
const WIDTH = width;
const HEIGHT = height;
const PLAYER_Y = playerY;
const INV_COLS = window.innerWidth < 768 ? 4 : 10; // Less columns on mobile
const INV_ROWS = window.innerWidth < 768 ? 5 : 3; // More rows on mobile
const INV_SPACING_X = invaderSpacingX;
const INV_SPACING_Y = invaderSpacingY;
const TICK_MS = window.innerWidth < 768 ? 120 : 120; // Slower on mobile
const BULLET_STEP = 20;
const INV_STEP = window.innerWidth < 768 ? 10 : 10; // Slower invader movement on mobile
const INV_DROP = window.innerWidth < 768 ? 10 : 20; // Slower drop on mobile

/* ---------------------------------------------------------------------- */
const LevelTransition = ({ level }) => (
  <motion.div
    initial={{ opacity: 0, scale: 0.5 }}
    animate={{ opacity: 1, scale: 1 }}
    exit={{ opacity: 0, scale: 1.5 }}
    style={{
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      backgroundColor: 'rgba(0, 0, 0, 0.7)',
      zIndex: 9999,
    }}
  >
    <motion.div
      initial={{ rotate: -10 }}
      animate={{ rotate: 10 }}
      transition={{ duration: 0.5, repeat: 3, repeatType: 'reverse' }}
      style={{
        padding: '2rem 3rem',
        backgroundColor: '#0f172a',
        border: '3px solid #38bdf8',
        borderRadius: '10px',
        boxShadow: '0 0 30px rgba(56, 189, 248, 0.6)',
        textAlign: 'center'
      }}
    >
      <h2 style={{
        fontSize: '32px',
        fontWeight: 'bold',
        marginBottom: '8px',
        color: '#ffffff',
        textShadow: '0 0 10px rgba(56, 189, 248, 0.8)'
      }}>
        Level {level}
      </h2>
      <p style={{
        fontSize: '18px',
        color: '#f59e0b'
      }}>
        Get Ready!
      </p>
    </motion.div>
  </motion.div>
);

export default function SpaceInvaders() {
  const [nodes, setNodes] = useNodesState([]);
  const [invaders, setInvaders] = useState([]);
  const [bullets, setBullets] = useState([]);
  const [playerX, setPlayerX] = useState(WIDTH / 2 - 20);
  const [dir, setDir] = useState(1);
  const [active, setActive] = useState(false);
  const [score, setScore] = useState(0);
  const [level, setLevel] = useState(1); // Add level state
  const [showLevelTransition, setShowLevelTransition] = useState(false); // Add state for level transition
  const levelRef = useRef(1); // Add level reference
  const scoreRef = useRef(0);
  const bulletsRef = useRef([]);
  const invadersRef = useRef([]);
  const dirRef = useRef(1);
  const [gameOver, setGameOver] = useState(false);
  const [gameWon, setGameWon] = useState(false);
  const [finalScore, setFinalScore] = useState(1000); // Set an initial score to display
  const [forceUpdate, setForceUpdate] = useState(0); // Add this for forcing re-render

   // Force a component re-render
  const forceRender = useCallback(() => {
    setForceUpdate((prev) => prev + 1);
    console.log("Forcing re-render");
  }, []);

  const lastShotRef = useRef(0);
  const SHOOT_COOLDOWN = window.innerWidth < 768 ? 200 : 100; // still throttle to avoid audio spam

  /* ------------------------------ shooting ------------------------------ */
  const playSound = useCallback((soundName) => {
    const sound = soundsRef.current[soundName];
    if (sound) {
      sound.currentTime = 0;
      sound.play().catch((e) => console.log("Sound play error:", e));
    }
  }, []);

  const handleShoot = useCallback(() => {
    if (!active) return;
    const now = Date.now();
    if (now - lastShotRef.current < SHOOT_COOLDOWN) return;
    lastShotRef.current = now;
    setBullets((bs) => [
      ...bs,
      {
        id: `b-${now}`,
        position: { x: playerX + 14, y: PLAYER_Y - 24 },
        draggable: false,
        data: { label: "🔺" },
        style: {
          background: "#facc15",
          width: 12,
          height: 20,
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          fontSize: 12,
          border: "none",
        },
      },
    ]);
    playSound("shoot");
  }, [active, playerX, playSound, SHOOT_COOLDOWN]);

  /* ------------------------------ sounds ------------------------------ */
  const soundsRef = useRef({});
  useEffect(() => {
    Object.entries(SOUNDS).forEach(([key, url]) => {
      soundsRef.current[key] = loadSound(url);
    });
  }, []);

  /* -------------------------------- helpers ---------------------------- */
  // Helper to calculate invader point value based on row and level
  const calculateInvaderPoints = (row, level, isSpecial = false) => {
    const rowBonus = Math.max(3 - row, 0) * 50;
    const levelBonus = (level - 1) * 25;
    const specialBonus = isSpecial ? 100 : 0;
    return 100 + rowBonus + levelBonus + specialBonus;
  };
  
  const makePlayer = useCallback(
    (x) => ({
      id: "player",
      position: { x, y: PLAYER_Y },
      draggable: false,
      data: { label: "🚀" },
      style: {
        background: "#0369a1",
        width: 40,
        height: 40,
        borderRadius: "50%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        fontSize: 22,
        color: "white",
        border: "none",
      },
    }),
    []
  );

  /* --------------------------------- init ----------------------------- */
  const initGame = (startNewGame = true) => {
    console.log("=== INIT GAME CALLED ===");
    
    // Reset level if starting a new game
    if (startNewGame) {
      setLevel(1);
      levelRef.current = 1;
    }
    
    // Calculate difficulty based on level
    const currentLevel = startNewGame ? 1 : levelRef.current;
    const invadersPerRow = Math.min(INV_COLS + Math.floor(currentLevel / 2), 14); // Add more invaders per level (max 14)
    const invaderRows = Math.min(INV_ROWS + Math.floor(currentLevel / 3), 6); // Add more rows per level (max 6)
    const invaderSpeed = INV_STEP + Math.floor(currentLevel / 2); // Increase invader speed with level
    
    const inv = [];
    let id = 0;
    for (let r = 0; r < invaderRows; r++) {
      for (let c = 0; c < invadersPerRow; c++) {
        // Determine invader type based on row and level
        const isSpecial = (currentLevel > 3 && r < 2) || (currentLevel > 2 && r === 0);
        const isElite = currentLevel > 4 && r === 0;
        const emoji = isElite ? "👾" : isSpecial ? "👿" : "👽";
        
        // Calculate point value for display in tooltip
        const pointValue = calculateInvaderPoints(r, currentLevel, isSpecial || isElite);
        
        inv.push({
          id: `inv-${id++}`,
          position: { x: 200 + c * INV_SPACING_X, y: 150 + r * INV_SPACING_Y },
          draggable: false,
          data: { 
            label: emoji,
            pointValue: pointValue
          },
          style: {
            background: isElite ? "#7e22ce" : isSpecial ? "#b91c1c" : "#0369a1",
            width: 40,
            height: 40,
            borderRadius: 8,
            fontSize: 20,
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            color: "white",
            border: "none",
            boxShadow: isElite ? "0 0 10px #a855f7" : isSpecial ? "0 0 5px #ef4444" : "none",
          },
        });
      }
    }
    
    console.log("Setting game state...");
    setInvaders(inv);
    setBullets([]);
    setPlayerX(WIDTH / 2 - 20);
    setDir(1);
    
    // Only reset score for new games
    if (startNewGame) {
      setScore(0);
    }
    
    setActive(true);
    setGameOver(false);
    setGameWon(false);
    setFinalScore(0);
    
    console.log("Game initialized successfully!");
    console.log(`New state will be: gameOver=false, gameWon=false, active=true, level: ${currentLevel}`);
    
    // Reset any debug flags
    window.gameDebug = true;
    playSound("gameStart");
  };

  /* ------------------------------ controls ----------------------------- */
  useEffect(() => {
    if (!active) return;

    const onKey = (e) => {
      if (e.key === "ArrowLeft") setPlayerX((x) => Math.max(10, x - 20));
      else if (e.key === "ArrowRight") setPlayerX((x) => Math.min(WIDTH - 50, x + 20));
      else if (e.key === " " || e.key === "ArrowUp") {
        e.preventDefault();
        handleShoot();
      }
    };
    window.addEventListener("keydown", onKey);
    return () => window.removeEventListener("keydown", onKey);
  }, [active, handleShoot]);

  /* ------------------------------ keep refs in sync --------------------------- */
  useEffect(() => {
    scoreRef.current = score;
  }, [score]);
  
  useEffect(() => {
    levelRef.current = level;
  }, [level]);

  useEffect(() => {
    bulletsRef.current = bullets;
  }, [bullets]);

  useEffect(() => {
    invadersRef.current = invaders;
  }, [invaders]);

  useEffect(() => {
    dirRef.current = dir;
  }, [dir]);

  /* ------------------------------ game loop --------------------------- */
  useEffect(() => {
    console.log("Game loop useEffect initiated");
    if (!active) return;
    
    // Calculate invader speed based on level
    const invaderSpeed = INV_STEP + Math.floor(levelRef.current / 2);
    
    const loop = setInterval(() => {
      console.log("Game loop tick"); // Added log

      // Get current values from refs to avoid stale closures
      const currentBullets = bulletsRef.current;
      const currentInvaders = invadersRef.current;
      const currentDir = dirRef.current;

      /* Move bullets */
      const movedBullets = currentBullets
        .map((b) => ({ ...b, position: { x: b.position.x, y: b.position.y - BULLET_STEP } }))
        .filter((b) => b.position.y > -30);

      /* Move invaders */
      const edgeHit = currentInvaders.some(
        (n) => n.position.x + currentDir * invaderSpeed < 10 || n.position.x + currentDir * invaderSpeed > WIDTH - 50
      );
      const newDir = edgeHit ? currentDir * -1 : currentDir;
      const movedInvaders = currentInvaders.map((n) => ({
        ...n,
        position: {
          x: n.position.x + newDir * invaderSpeed,
          y: n.position.y + (edgeHit ? INV_DROP : 0),
        },
      }));

      /* Collision detection */
      const remainingInvaders = [];
      let hitCount = 0;
      let hitPoints = 0; // Track points separately from count
      
      movedInvaders.forEach((inv) => {
        const hit = movedBullets.some((b) => {
          const withinX = b.position.x >= inv.position.x && b.position.x <= inv.position.x + 40;
          const withinY = b.position.y >= inv.position.y && b.position.y <= inv.position.y + 40;
          return withinX && withinY;
        });
        
        if (!hit) {
          remainingInvaders.push(inv);
        } else {
          hitCount += 1;
          
          // Use the point value stored in the invader's data
          // If not available (for backwards compatibility), calculate it
          const points = inv.data.pointValue || 100;
          hitPoints += points;
        }
      });

      if (hitCount) {
        setScore((s) => {
          const newScore = s + hitPoints;
          scoreRef.current = newScore; // Update ref immediately
          return newScore;
        });
        playSound("explosion");
      }

      /* Remove bullets that hit */
      const survivingBullets = movedBullets.filter((b) => {
        return !remainingInvaders.some((inv) => {
          const withinX = b.position.x >= inv.position.x && b.position.x <= inv.position.x + 40;
          const withinY = b.position.y >= inv.position.y && b.position.y <= inv.position.y + 40;
          return withinX && withinY;
        });
      });

      // Update state
      setBullets(survivingBullets);
      setInvaders(remainingInvaders);
      setDir(newDir);

      //.log("Remaining invaders count:", remainingInvaders.length);

      /* Win / lose checks */
      if (remainingInvaders.some((n) => n.position.y >= PLAYER_Y - 20)) {
        // Game over sequence
        const final = scoreRef.current;
        setActive(false);
        setGameOver(true);
        setFinalScore(final);
        
        // Make sure we see the modal by forcing a focus
        setTimeout(() => {
          document.body.style.overflow = "hidden"; // Prevent scrolling behind modal
          
          // Force re-render
          forceRender();
        }, 100);
        
        playSound("gameOver");
      }
      if (remainingInvaders.length === 0) {
        // Level completed sequence!
        playSound("gameWin");
        
        // Update level
        const newLevel = levelRef.current + 1;
        setLevel(newLevel);
        levelRef.current = newLevel;
        
        // Show level transition
        setActive(false); // Pause game during transition
        setShowLevelTransition(true);
        
        // Short pause before starting the next level
        setTimeout(() => {
          setShowLevelTransition(false);
          // Start next level without resetting score
          initGame(false);
        }, 3000);
      }
    }, TICK_MS);

    return () => clearInterval(loop);
  }, [active, playSound, forceRender, invaders]); // Added invaders to dependency array

  const playerNode = useMemo(() => makePlayer(playerX), [makePlayer, playerX]);

  useEffect(() => {
    // Add tooltips to invaders showing their point value
    const nodesWithTooltips = [
      playerNode,
      ...invaders.map(inv => ({
        ...inv,
        // For desktop, add a title to show points on hover
        data: {
          ...inv.data,
          title: window.innerWidth >= 768 ? `${inv.data.pointValue} pts` : undefined
        }
      })),
      ...bullets
    ];
    setNodes(nodesWithTooltips);
  }, [playerNode, invaders, bullets, setNodes]);

  /* ------------------------------- render ----------------------------- */
  return (
    <>
      <div style={{ 
        height: "100vh", 
        width: "100%", 
        display: "flex", 
        flexDirection: "column", 
        color: "white", 
        overflow: "visible",  // Allow modal to overflow
        backgroundColor: "#000000"
      }}>
        <motion.div
          initial={{ y: -50, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          style={{
            textAlign: "center",
            backgroundColor: "#0f172a",
            padding: "12px 0",
            borderBottom: "2px solid #38bdf8",
            boxShadow: "0 4px 12px rgba(56, 189, 248, 0.4)",
            fontFamily: "'Press Start 2P', monospace, system-ui"
          }}
        >
          <h1 style={{
            margin: 0,
            fontSize: window.innerWidth < 768 ? "24px" : "32px",
            background: "linear-gradient(to right, #38bdf8, #818cf8)",
            WebkitBackgroundClip: "text",
            WebkitTextFillColor: "transparent",
            fontWeight: "bold",
            textShadow: "2px 2px 4px rgba(0, 0, 0, 0.5)",
            letterSpacing: "2px"
          }}>
            SPACE INVADERS
          </h1>
        </motion.div>
        <motion.div
          initial={{ y: -50, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          style={{
            padding: "16px",
            display: "flex",
            gap: "16px",
            alignItems: "center",
            backgroundColor: "#000000",
            boxShadow: "0 4px 6px -1px rgba(0, 0, 0, 0.1)",
            zIndex: 10,
            borderBottom: "1px solid rgb(51, 51, 51)"
          }}
        >
          <Button type="button" onClick={(e) => { e.preventDefault(); initGame(); }}>
            {active ? "Restart" : "Start"}
          </Button>
          
          <div style={{ marginLeft: "auto", display: "flex", gap: "16px" }}>
            <span style={{ fontSize: "14px", fontWeight: "600", color: "#3b82f6" }}>Level: {level}</span>
            <span style={{ fontSize: "14px", fontWeight: "600", color: "green" }}>Score: {score}</span>
          </div>
        </motion.div>

        <div style={{ flex: 1, position: "relative", width: "100%", backgroundColor: "#000000" }}>
          
          <div style={{ width: "100vw", height: "80vh", background: "#000000" }}>
            
            <ReactFlow
              nodes={nodes}
              edges={[]}
              panOnDrag={false}
              zoomOnScroll={false}
              panOnScroll={false}
              minZoom={window.innerWidth < 768 ? 0.4 : 0.2}
              maxZoom={1}
              defaultViewport={{
                x: 0,
                y: 0,
                zoom: Math.min(window.innerWidth / totalWidth, zoom),
              }}
            >
              <Background gap={40} size={1.5} color="#333333" variant="lines" style={{ opacity: 0.4 }} />
            </ReactFlow>
          </div>
        </div>

        {active && window.innerWidth < 768 && (
          <div style={{
            height: "128px", 
            backgroundColor: "#000000", 
            display: "flex", 
            justifyContent: "center", 
            alignItems: "center",
            borderTop: "1px solid rgb(51, 51, 51)"
          }}>
            <div style={{
              display: "flex", 
              justifyContent: "space-between", 
              alignItems: "center",
              width: "100%",
              maxWidth: "340px"
            }}>
              <button
                onTouchStart={() => setPlayerX((x) => Math.max(10, x - 20))}
                onTouchEnd={(e) => e.preventDefault()}
                style={{ 
                  width: "64px", 
                  height: "64px", 
                  borderRadius: "50%", 
                  backgroundColor: "rgba(59, 130, 246, 0.5)", 
                  display: "flex", 
                  justifyContent: "center", 
                  alignItems: "center", 
                  color: "white", 
                  fontSize: "28px", 
                  border: "none",
                  padding: 0,
                  touchAction: "manipulation"
                }}
              >
                ←
              </button>
              <button
                onPointerDown={handleShoot}
                onTouchStart={handleShoot}
                style={{ 
                  width: "64px", 
                  height: "64px", 
                  borderRadius: "50%", 
                  backgroundColor: "rgba(59, 130, 246, 0.5)", 
                  display: "flex", 
                  justifyContent: "center", 
                  alignItems: "center", 
                  color: "white", 
                  fontSize: "28px", 
                  border: "none",
                  padding: 0,
                  touchAction: "manipulation"
                }}
              >
                🔥
              </button>
              <button
                onTouchStart={() => setPlayerX((x) => Math.min(WIDTH - 50, x + 20))}
                onTouchEnd={(e) => e.preventDefault()}
                style={{ 
                  width: "64px", 
                  height: "64px", 
                  borderRadius: "50%", 
                  backgroundColor: "rgba(59, 130, 246, 0.5)", 
                  display: "flex", 
                  justifyContent: "center", 
                  alignItems: "center", 
                  color: "white", 
                  fontSize: "28px", 
                  border: "none",
                  padding: 0,
                  touchAction: "manipulation"
                }}
              >
                →
              </button>
            </div>
          </div>
        )}
      </div>
      
      {/* Level transition overlay */}
      <AnimatePresence>
        {showLevelTransition && <LevelTransition level={level} />}
      </AnimatePresence>
      
      {(gameOver || gameWon) && (
        <GameOverModal
          score={finalScore}
          level={level} // Pass level to GameOverModal
          isWin={gameWon}
          onRestart={() => {
            // Reset body overflow and remove debug element when restarting
            document.body.style.overflow = "auto";
            
            initGame();
          }}
          onClose={() => {
            // Reset body overflow and remove debug element when closing
            document.body.style.overflow = "auto";

            setActive(false); // Ensure game is not active
            setGameOver(false);
            setGameWon(false);
          }}
        />
      )}
    </>
  );
}
